<template>
  <div>
    <div v-for="user in users" v-bind:key="user._id">
      <p>{{user.first_name}}</p>
      <p>{{user.last_name}}</p>
      <p>{{user.bio}}</p>
      <p>{{user.photo}}</p>
    </div>
  </div>
</template>

<script>
import UserService from '../services/UserService'

export default {
  name: 'CompanyProfile',
  data () {
    return {
      users: [],
      errors: ''
    }
  },
  async created () {
    try {
      this.users = await UserService.getUsers()
    } catch (err) {
      this.error = err.message
    }
  }
}
</script>
