import axios from 'axios'

const url = 'api/users/'

class UserService {
  static getUsers () {
    return new Promise(async (resolve, reject) => {
      try {
        const res = await axios.get(url)
        const data = res.data
        resolve(data.users)
      } catch (err) {
        reject(err)
      }
    })
  }

  static addUser (firstName, lastName, bio, photo) {
    return axios.post(url, {
      firstName: firstName,
      lastName: lastName,
      bio: bio,
      photo: photo
    })
  }

  static deleteUser (id) {
    return axios.delete(`${url}${id}`)
  }
}

export default UserService
